import React from 'react'
import oneimg from '../images/hhhh.png';
import twoimg from '../images/Untitled-4.png';
import  threeimg from '../images/Untitled-5.png';
import imgfor from '../images/Untitled-6.png'
import { Col, Container, Row } from 'react-bootstrap'

const About = () => {
  return (
    <div style={{backgroundColor:"#099B93" }} >
    <Container>

      <Row className="">
        <div className='d-flex justify-content-center my-3 OnlineText'> </div>
      </Row>

      <Row className="">
        <div className='d-flex justify-content-around my-3 OnlineTextOne mt-4'>         <br/>            </div>
      </Row>
      
      


  <Row className='pb-5' >
      <Col sm="6" lg="3">
          <img src={oneimg} />
          <p className='OnlineTextOne'>     <br/>    </p>
      </Col>
      <Col sm="6" lg="3">
          <img src={twoimg} />
          <p className='OnlineTextOne'>    <br/>   </p>
      </Col>
      <Col sm="6" lg="3">
          <img src={imgfor} />
          <p className='OnlineTextOne'>      </p>
      </Col>
      <Col sm="6" lg="3">
          <img src={threeimg}  />
          <p className='OnlineTextOne'  >           </p>
      </Col>
  </Row>


    </Container>

    </div>
  )
}

export default About
