import React from 'react'
import { Row,Col, Container } from 'react-bootstrap'
import paybal from '../images/paypal.png'
const ChoosePayMethoud = () => {
    return (
      
        <Container style={{minHeight:"568px"}}>
            <div className="admin-content-text pt-5">  </div>
            <div className="user-address-card my-3 px-3">
                <Row className="d-flex justify-content-between ">
                    <Col xs="12" className="my-4">
                    <div className='d-flex'>

                        <input
                            name="group"
                            id="group1"
                            type="radio"
                            value="   "
                            className=""
                        />
                        <label className="mx-2" for="group1">
                                
                        </label>

                        <img style={{width:"100px"}} src={paybal}/>
                    </div>
                        
                    </Col>
                </Row>

                <Row className="mt-3">
                    <Col xs="12" className="d-flex">
                        <input
                            name="group"
                            id="group1"
                            type="radio"
                            value="  "
                            className="mt-2"
                        />
                        <label className="mx-2" for="group1">
                              
                        </label>
                    </Col>
                </Row>
            </div>

            <Row>
                <Col xs="12" className="d-flex justify-content-end">
                    <div className="product-price d-inline   border">  :1000 </div>
                    <div className="product-price d-inline   border me-2">  :500 </div>
                    <div className="product-cart-add px-3 pt-2 d-inline me-2" style={{backgroundColor:"#099B93"}}>  </div>
                </Col>
            </Row>
            </Container>
      
    )
}

export default ChoosePayMethoud